import { useContext, useEffect, useState } from "react";
import { AppContext } from "../AppProvider";
import FairnessSeeds from "./FairnessSeeds";
import FairnessVerify from "./FairnessVerify";



const Fairness = () => {
    const {setFairnessOpen} = useContext(AppContext)
    const [tab, setTab] = useState('seeds')

    return <>
        <div className="fixed w-full h-full top-0 left-0 right-0 bg-[#000000c7] flex items-center justify-center px-2">
            <div className="relative w-full max-w-[500px] bg-[#213743] rounded-[.8rem] overflow-hidden overflow-y-auto flex flex-col max-h-[500px] thinScrollbar">
                <button onClick={() => setFairnessOpen(false)} className="absolute right-[10px] top-[10px] text-[#d9d9d9] hover:text-white text-[1.2rem] cursor-pointer p-2 bg-transparent leading-4">✖</button>
                <h1 className="text-[#d9d9d9] text-[1.2rem] font-bold mb-2 p-4">Fairness</h1>

                <div className="flex justify-center px-4 py-2">
                    <div className="bg-[#0f212e] p-1 rounded-[.8rem] flex gap-1.5 text-[#d9d9d9] font-semibold">
                        <button onClick={() => setTab('seeds')} className={`transition-all hover:bg-[#2f4553] px-[1.2rem] py-[0.7rem] rounded-[.8rem] ${tab === 'seeds' ? 'bg-[#2f4553]' : 'bg-transparent'}`} >Seeds</button>
                        <button onClick={() => setTab('verify')} className={`transition-all hover:bg-[#2f4553] px-[1.2rem] py-[0.7rem] rounded-[.8rem] ${tab === 'verify' ? 'bg-[#2f4553]' : 'bg-transparent'}`} >Verify</button>
                    </div>
                </div>

                { tab === 'seeds' && <FairnessSeeds/> }
                { tab === 'verify' && <FairnessVerify/> }

            </div>
        </div>
    </>
}

export default Fairness