import { useContext, useEffect, useState } from "react";
import { AppContext } from "../AppProvider";
import Copy from "../svgs/icons/Copy";
import config from '../config.json'


const copyToClipboard = (text) => {
    navigator.clipboard.writeText(text).then(() => {}).catch(err => {})
}

const FairnessSeeds = () => {
    const {authData, setFairnessOpen} = useContext(AppContext)
    const [activeSeed, setActiveSeed] = useState(null)

    useEffect( () => {
        if(!authData) return

        ;( async() => {
            const response = await fetch(config.BASE_URL+'/api/active-seed', {
                headers: {
                    'authorization': 'Bearer ' + authData,
                    'content-type': 'application/json;charset=utf-8'
                },
                method: 'GET'
            }).catch( () => ({ ok: false}) )
            if(!response.ok) return

            const data = await response.json()
            console.log('data:', data)
            setActiveSeed(data)

        } )()

    }, [authData] )


    const handleRotateSeed = async () => {
        const response = await fetch(config.BASE_URL+'/api/rotate-seed', {
            headers: {
                'authorization': 'Bearer ' + authData,
                'content-type': 'application/json;charset=utf-8'
            },
            method: 'POST'
        }).catch( () => ({ ok: false}) )
        if(!response.ok) return

        const data = await response.json()
        setActiveSeed(data)
        console.log('data:', data)
    }

    return <>
        <div className="flex flex-col px-4 py-2">
            <label htmlFor='clientSeed' className="text-[#d9d9d9] font-semibold text-[1rem] w-fit">Active Client Seed</label>
            <div className="flex w-full relative items-center" style={{boxShadow: '0 0 6px 1px #00000024'}}>
                <input
                    readOnly
                    className='cursor-text text-[#d9d9d9] bg-[#0f212e] font-semibold py-2 px-2 outline-none rounded-l-[6px] w-full transition-all border-2 border-[#213743] focus:border-[#557086] hover:border-[#557086]'
                    name="clientSeed" id="clientSeed" type="text" value={activeSeed?.clientSeed || '...'}
                />
                <button onClick={() => copyToClipboard(activeSeed?.clientSeed)} className="p-2.5 py-[11px] h-full flex items-center justify-center text-[#d9d9d9] bg-[#0f212e] rounded-r-[6px] border-2 border-[#213743] hover:border-[#557086]">
                    <Copy className="h-[18px] w-[18px]"/>
                </button>
            </div>
        </div>

        <div className="flex flex-col px-4 py-2">
            <label htmlFor='serverSeedH' className="text-[#d9d9d9] font-semibold text-[1rem] w-fit">Active Server Seed (Hashed)</label>
            <div className="flex w-full relative items-center" style={{boxShadow: '0 0 6px 1px #00000024'}}>
                <input
                    readOnly
                    className='cursor-text text-[#d9d9d9] bg-[#0f212e] font-semibold py-2 px-2 outline-none rounded-l-[6px] w-full transition-all border-2 border-[#213743] focus:border-[#557086] hover:border-[#557086]'
                    name="serverSeedH" id="serverSeedH" type="text" value={activeSeed?.serverSeedHashed || '...'}
                />
                <button onClick={() => copyToClipboard(activeSeed?.serverSeedHashed)} className="p-2.5 py-[11px] h-full flex items-center justify-center text-[#d9d9d9] bg-[#0f212e] rounded-r-[6px] border-2 border-[#213743] hover:border-[#557086]">
                    <Copy className="h-[18px] w-[18px]"/>
                </button>
            </div>
        </div>

        <div className="flex flex-col px-4 py-2">
            <label htmlFor='nonce' className="text-[#d9d9d9] font-semibold text-[1rem] w-fit">Total bets made with pair</label>
            <div className="flex w-full relative items-center" style={{boxShadow: '0 0 6px 1px #00000024'}}>
                <input
                    readOnly
                    className='cursor-text text-[#d9d9d9] bg-[#0f212e] font-semibold py-2 px-2 outline-none rounded-l-[6px] w-full transition-all border-2 border-[#213743] focus:border-[#557086] hover:border-[#557086]'
                    name="nonce" id="nonce" type="text" value={activeSeed?.nonce}
                />
                <button onClick={() => copyToClipboard(activeSeed?.nonce)} className="p-2.5 py-[11px] h-full flex items-center justify-center text-[#d9d9d9] bg-[#0f212e] rounded-r-[6px] border-2 border-[#213743] hover:border-[#557086]">
                    <Copy className="h-[18px] w-[18px]"/>
                </button>
            </div>
        </div>

        <div className="w-full mt-4 p-4 bg-[#0f212e]">
            <button
                onClick={handleRotateSeed}
                className='betButton w-full bg-[#1475e1] hover:bg-[#227ee3] disabled:bg-[#164982] font-semibold text-[#d9d9d9] py-2.5 rounded-[6px] transition-all select-none'
                style={{boxShadow: '0 0 6px 1px #00000024'}}
            >Rotate Seed Pair</button>
        </div>
    </>
}

export default FairnessSeeds