import { useContext, useEffect, useState } from 'react'
import Plinko from './pages/Plinko/Plinko'
import { AppContext } from './AppProvider'
import Fairness from './components/Fairness'

function App() {
  const { isAuth, authLoaded, fairnessOpen } = useContext(AppContext)


  //if(!authLoaded) return false
  return (
    <>
      <div className={`w-full h-full ${!isAuth.state ? 'overflow-hidden' : 'overflow-y-auto'}`}>
        <Plinko/>
        {
          !isAuth.state ? (
            <div className="fixed left-0 right-0 bottom-0 top-0 bg-[#000000c7] flex items-center justify-center overflow-hidden px-8">
              <div className="w-full max-w-[900px]">
                <h1 className="text-[2.5rem] max-md:text-[1.5rem] text-[#b3b3b3] text-center">{isAuth.message}</h1>
              </div>
            </div>
          ) : false 
        }
      </div>
      {
        fairnessOpen ? (
          <Fairness/>
        ) : false
      }
    </>
  )
}

export default App
