import { useContext, useEffect, useState } from "react"
import { AppContext } from "../../../AppProvider"
import HistoryItem from "./HistoryItem"
import config from '../../../config.json'

const History = ({gamesList}) => {
    const {authData} = useContext(AppContext)
    

    useEffect( () => {
        if(!authData) return

        ;( async() => {
            const response = await fetch(config.BASE_URL+'/api/history/plinko', {
                headers: {
                    'authorization': 'Bearer ' + authData,
                    'content-type': 'application/json;charset=utf-8'
                },
                method: 'POST'
            }).catch( () => ({ ok: false}) )
            if(!response.ok) return

            const data = await response.json()
            console.log('data:', data)

        } )()

    }, [authData] )

    if(!gamesList.length) return false
    return <>
        <div className="w-full flex justify-center my-8">
            <div className="w-full px-6 max-[925px]:max-w-[500px] max-w-[1050px] mx-10 max-sm:mx-2 py-4 rounded-[.8rem] flex items-center justify-between text-[#d9d9d9] bg-[#213743]">
                <table className="table-auto w-full history-table max-sm:text-[.7rem]">
                    <thead >
                        <tr>
                            <th className="text-left p-4 max-sm:px-1.5">Game</th>
                            <th className="p-4 text-center max-sm:px-1.5">Bet Amount</th>
                            <th className="p-4 text-center max-sm:px-1.5">Multiplier</th>
                            <th className="text-right p-4 max-sm:px-1.5">Payout</th>
                        </tr>
                    </thead>
                    <tbody>
                        {
                            gamesList.map( game =>
                                <HistoryItem key={game.id} game={game} />
                            )
                        }
                    </tbody>
                </table>
            </div>
        </div>
    </>
}

export default History