import { useState } from "react"
import ProvableFairness from "../../../components/ProvableFairness"

const HistoryItem = ({game}) => {
    const [pFairnessOpen, setPFairnessOpen] = useState(false)
    return <>
        <tr>
            <td className="p-4 max-sm:px-1.5 text-left font-bold"><span onClick={() => setPFairnessOpen(true)} className="cursor-pointer hover:underline select-none">{game.game}</span></td>
            <td className="p-4 max-sm:px-1.5 text-center">${parseFloat(game.amount ).toFixed(2)}</td>
            <td className="p-4 max-sm:px-1.5 text-center">{game.multiplayer}×</td>
            <td style={{color: game.multiplayer >= 1 ? '#00e701' : '#e70000'}} className="p-4 max-sm:px-1.5 text-right">${game.multiplayer < 1 ? ''/*'-'*/ : ''}{parseFloat(game.amount * game.multiplayer).toFixed(2)}</td>
        </tr>
        {
            pFairnessOpen ? (
                <ProvableFairness game={game} setPFairnessOpen={setPFairnessOpen}/>
            ) : false
        }
        
    </>
}

export default HistoryItem