import { useContext, useEffect, useState } from "react";
import { AppContext } from "../AppProvider";
import Copy from "../svgs/icons/Copy";
import config from '../config.json'

const copyToClipboard = (text) => {
    navigator.clipboard.writeText(text).then(() => {}).catch(err => {})
}

const ProvableFairness = ({game, setPFairnessOpen}) => {
    const {authData} = useContext(AppContext)
    const [serverSeed, setServerSeed] = useState(null)

    useEffect( () => {
        if(!authData) return

        ;( async() => {
            const response = await fetch(config.BASE_URL+'/api/game-seed', {
                headers: {
                    'authorization': 'Bearer ' + authData,
                    'content-type': 'application/json;charset=utf-8'
                },
                body: JSON.stringify({gameId: game.id, gameName: game.game}),
                method: 'POST'
            }).catch( () => ({ ok: false}) )
            if(!response.ok) return

            const data = await response.json()
            setServerSeed(data.game.gameData.serverSeed)
            console.log('data:', data)
        } )()

    }, [authData] )

    return <>
        <div className="fixed w-full h-full top-0 left-0 right-0 bg-[#000000c7] flex items-center justify-center px-2">
            <div className="relative w-full max-w-[500px] bg-[#213743] rounded-[.8rem] overflow-hidden flex flex-col">
                <button onClick={() => setPFairnessOpen(false)} className="absolute right-[10px] top-[10px] text-[#d9d9d9] hover:text-white text-[1.2rem] cursor-pointer p-2 bg-transparent">✖</button>
                <h1 className="text-[#d9d9d9] text-[1.2rem] font-bold mb-2 p-4">Provable Fairness</h1>

                <div className="flex flex-col px-4 py-2">
                    <label htmlFor='clientSeed' className="text-[#d9d9d9] font-semibold text-[1rem] w-fit">Client Seed</label>
                    <div className="flex w-full relative items-center" style={{boxShadow: '0 0 6px 1px #00000024'}}>
                        <input
                            readOnly
                            className='cursor-text text-[#d9d9d9] bg-[#0f212e] font-semibold py-2 px-2 outline-none rounded-l-[6px] w-full transition-all border-2 border-[#213743] focus:border-[#557086] hover:border-[#557086]'
                            name="clientSeed" id="clientSeed" type="text" value={game?.gameData?.clientSeed || '...'}
                        />
                        <button onClick={() => copyToClipboard(game?.gameData?.clientSeed || '')} className="p-2.5 py-[11px] h-full flex items-center justify-center text-[#d9d9d9] bg-[#0f212e] rounded-r-[6px] border-2 border-[#213743] hover:border-[#557086]">
                            <Copy className="h-[18px] w-[18px]"/>
                        </button>
                    </div>
                </div>


                <div className="flex flex-col px-4 py-2">
                    <label htmlFor='serverSeed' className="text-[#d9d9d9] font-semibold text-[1rem] w-fit">Server Seed</label>
                    <div className="flex w-full relative items-center" style={{boxShadow: '0 0 6px 1px #00000024'}}>
                        <input
                            readOnly
                            className='disabled:opacity-[.5] disabled:cursor-not-allowed disabled:rounded-r-[6px] cursor-text text-[#d9d9d9] bg-[#0f212e] font-semibold py-2 px-2 outline-none rounded-l-[6px] w-full transition-all border-2 border-[#213743] focus:border-[#557086] hover:border-[#557086]'
                            name="serverSeed" id="serverSeed" type="text" disabled={!game?.gameData?.serverSeed && !serverSeed} value={game?.gameData?.serverSeed || serverSeed || 'Rotate your seed pair to verify this bet'}
                        />
                        {
                            !!game?.gameData?.serverSeed || !!serverSeed ? (
                                <button onClick={() => copyToClipboard(game?.gameData?.serverSeed || serverSeed || '')} className="p-2.5 py-[11px] h-full flex items-center justify-center text-[#d9d9d9] bg-[#0f212e] rounded-r-[6px] border-2 border-[#213743] hover:border-[#557086]">
                                    <Copy className="h-[18px] w-[18px]"/>
                                </button>
                            ) : false
                        }

                    </div>
                </div>

                <div className="flex flex-col px-4 py-2">
                    <label htmlFor='serverSeedH' className="text-[#d9d9d9] font-semibold text-[1rem] w-fit">Server Seed (Hashed)</label>
                    <div className="flex w-full relative items-center" style={{boxShadow: '0 0 6px 1px #00000024'}}>
                        <input
                            readOnly
                            className='cursor-text text-[#d9d9d9] bg-[#0f212e] font-semibold py-2 px-2 outline-none rounded-l-[6px] w-full transition-all border-2 border-[#213743] focus:border-[#557086] hover:border-[#557086]'
                            name="serverSeedH" id="serverSeedH" type="text" value={game?.gameData?.serverSeedHashed || '...'}
                        />
                        <button onClick={() => copyToClipboard(game?.gameData?.serverSeedHashed || '')} className="p-2.5 py-[11px] h-full flex items-center justify-center text-[#d9d9d9] bg-[#0f212e] rounded-r-[6px] border-2 border-[#213743] hover:border-[#557086]">
                            <Copy className="h-[18px] w-[18px]"/>
                        </button>
                    </div>
                </div>

                <div className="flex flex-col px-4 py-2 mb-3">
                    <label htmlFor='nonce' className="text-[#d9d9d9] font-semibold text-[1rem] w-fit">Nonce</label>
                    <div className="flex w-full relative items-center" style={{boxShadow: '0 0 6px 1px #00000024'}}>
                        <input
                            readOnly
                            className='cursor-text text-[#d9d9d9] bg-[#0f212e] font-semibold py-2 px-2 outline-none rounded-l-[6px] w-full transition-all border-2 border-[#213743] focus:border-[#557086] hover:border-[#557086]'
                            name="nonce" id="nonce" type="text" value={game?.gameData?.nonce || '...'}
                        />
                        <button onClick={() => copyToClipboard(game?.gameData?.nonce || '')} className="p-2.5 py-[11px] h-full flex items-center justify-center text-[#d9d9d9] bg-[#0f212e] rounded-r-[6px] border-2 border-[#213743] hover:border-[#557086]">
                            <Copy className="h-[18px] w-[18px]"/>
                        </button>
                    </div>
                </div>
            </div>
        </div>
    </>
}

export default ProvableFairness